import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export class Appoinments extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data:[]
        }
    }

    componentDidMount() {
        fetch('http://localhost:3002/showappointment',{
            method: 'POST',
            headers: {'content-type': 'application/json'},
            body:JSON.stringify({
                user: sessionStorage.getItem('user')
            })
        }).then(res => res.json())
        .then(data=>{
            this.setState({data:data});
            // this.componentDidMount()
        })
    }

    render() {
        return (
            <div className='container-fluid'>
                <div className='row mt-2'>
                    <div className='col'>
                        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                            <div className="container-fluid m-2">
                                <a className="navbar-brand h4" href="#">Appoinments</a>
                                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                                    <span className="navbar-toggler-icon"></span>
                                </button>
                                <div className="collapse navbar-collapse" id="navbarNav">
                                    <ul className="navbar-nav">
                                        <li className="nav-item">
                                            <Link className="nav-link active" aria-current="page" to="/calendar">Home</Link>
                                        </li>
                                        <li className="nav-item">
                                            <Link className="nav-link active" to="/eventform">Book Appoinments</Link>
                                        </li>
                                        <li class="nav-item">
                                            <Link className="nav-link active" to="/">Logout</Link>
                                        </li>
                                        <li class="nav-item">
                                            <a className="nav-link " href="#" tabindex="-1" aria-disabled="true">Profile</a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </nav>
                    </div>
                </div>
                <div>
                    <div className='row'>
                        <div className='col m-2'>
                            <div className='card p-3'>
                                <h2>Your Appoinments</h2>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th scope="col">S.NO</th>
                                            <th scope="col">Email</th>
                                            <th scope="col">Description</th>
                                            <th scope="col">Date</th>
                                            <th scope="col">Slot</th>
                                            <th scope="col">Action</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {
                                            this.state.data.map(e=>{
                                                return(
                                                    <tr>
                                                    <th scope="row">{e.id}</th>
                                                    <td>{e.username}</td>
                                                    <td>{e.descr}</td>
                                                    <td>{e.date}</td>
                                                    <td>{e.timeslot}</td>
                                                    <td className='btn btn-danger bg-danger m-1'>Delete</td>
                                                    
                                                </tr>
                                                )
                                            })
                                        }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

